

.Table {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
}

.tableContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
}



.searchValue {
    margin-top: 5px;
    cursor: pointer;
}

.searchValuesBlok {
    display: grid;
    grid-template-columns: 14rem 14rem 14rem;
    gap: 10px;
    font-size: 11px;
}